.options-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -100px;
}
.options-container-content {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.option {
  cursor: pointer;
  background-color: var(--color_principal);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  min-width: 200px;
  width: 200px;
  min-height: 100px;
  height: 100px;
  padding: 20px;
  margin: 20px;
  border-radius: 20px;
  box-shadow: 0px -1px 17px -5px rgba(0, 0, 0, 0.64);
  -webkit-box-shadow: 0px -1px 17px -5px rgba(0, 0, 0, 0.64);
  -moz-box-shadow: 0px -1px 17px -5px rgba(0, 0, 0, 0.64);
  transition: 0.3s;
  z-index: 2;
}

.option:hover {
  background-color: var(--color_principal_claro);
}
.option:hover .option_content {
  animation: none;
}
.option:hover .option_text {
  visibility: hidden;
  opacity: 0;
  animation: none;
  transition: visibility 0.2s, opacity 0.5s;
}

.option:hover .icon {
  margin-top: 30px;
  font-size: 80px;
  animation: none;
}

.option_content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.option_text {
  font-size: 30px;
  text-transform: uppercase;
  font-weight: bolder;
}
.icon {
  font-size: 50px;
  transition: 0.3s;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .options-container {
    flex-direction: column;
  }
}
