.recent-videos{
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.react-player{
  width: 100px;
}
.video{
  transform: scale(0.9);
}
.videos_component{
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .video{
    margin-top: -100px;
    transform: scale(0.5);
  }
}