.container_banner{
  background: linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(204, 33, 33, 0.73)),url('assets/images/image_optimized.jpg');
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  height: 700px;
  width: 100%;
  flex-direction: column;
  align-items: center;
  padding-top: 80px;
  position: relative;
  z-index: 1;
  background-position: 0px -100px;
  transition: background-image 0.2s ease-in-out;
  overflow-x:hidden;
  overflow-y:hidden;
}

.wave{
  position: absolute;
  left: 50;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.logo{
  position: relative;
  padding: 60px;
  width: 20%;
  cursor: pointer;
  transition: 0.5s;
  z-index: 2;
  min-width: 350px;

}

.logo:hover{
  transform: scale(1.2) rotate(-5deg);
}


.fa-chevron-down {
  color: #000000 !important;
  bottom: 10px;
  cursor: pointer;
  transition: 1.2s ease;
  margin-bottom: 300px;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .logo{
    padding: 30px;
    width: 50%;
    min-width: 350px;
    cursor: pointer;
    transition: 0.5s;
  }
  .container_banner{
    background-position: 50%;
  }
  .wave{
    width: auto;
  }
}